/* About Hero Section */
.about-hero {
  padding: 8rem 0 4rem;
  background: var(--light-gray);
}

.about-hero-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
}

.hero-text .section-tag {
  display: inline-block;
  background: var(--sage-green);
  color: var(--white);
  padding: 0.5rem 1rem;
  border-radius: 1.25rem;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin-bottom: 1.5rem;
}

.hero-text h1 {
  font-size: clamp(2.5rem, 5vw, 3.5rem);
  font-weight: 900;
  color: var(--dark-gray);
  margin-bottom: 1.5rem;
  line-height: 1.1;
}

.hero-text p {
  font-size: 1.25rem;
  color: var(--text-light);
  margin-bottom: 3rem;
  line-height: 1.6;
}

.hero-stats {
  display: flex;
  gap: 2rem;
  flex-wrap: wrap;
}

.hero-stat {
  text-align: center;
}

.stat-numberr {
  display: block;
  font-size: 2.5rem;
  font-weight: 900;
  color: var(--sage-green);
  line-height: 1;
}

.stat-labell {
  font-size: 0.875rem;
  color: var(--text-light);
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.hero-image img {
  width: 100%;
  height: 400px;
  object-fit: cover;
  border-radius: 1.5rem;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
}

/* Our Story Section */
.our-story {
  background: var(--white);
}

.story-content {
  display: grid;
  grid-template-columns: 1.5fr 1fr;
  gap: 4rem;
  align-items: start;
}

.story-text h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 2rem;
}

.story-text p {
  font-size: 1.125rem;
  line-height: 1.7;
  color: var(--text-light);
  margin-bottom: 1.5rem;
}

.story-highlights {
  margin-top: 3rem;
}

.highlight {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  margin-bottom: 2rem;
}

.highlight-icon {
  width: 3rem;
  height: 3rem;
  background: var(--sage-green);
  border-radius: 0.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--white);
  flex-shrink: 0;
}

.highlight h4 {
  font-size: 1.125rem;
  font-weight: 600;
  color: var(--dark-gray);
  margin-bottom: 0.5rem;
}

.highlight p {
  color: var(--text-light);
  font-size: 0.875rem;
  margin: 0;
}

/* Timeline */
.story-timeline h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 2rem;
  text-align: center;
}

.timeline {
  position: relative;
}

.timeline::before {
  content: '';
  position: absolute;
  left: 1rem;
  top: 0;
  bottom: 0;
  width: 2px;
  background: var(--primary-green);
}

.timeline-item {
  position: relative;
  margin-bottom: 3rem;
  padding-left: 3rem;
}

.timeline-year {
  position: absolute;
  left: 0;
  top: 0;
  width: 2rem;
  height: 2rem;
  background: var(--sage-green);
  color: var(--white);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: 600;
  transform: translateX(-50%);
}

.timeline-content h4 {
  font-size: 1.125rem;
  font-weight: 600;
  color: var(--dark-gray);
  margin-bottom: 0.5rem;
}

.timeline-content p {
  color: var(--text-light);
  font-size: 0.875rem;
  margin: 0;
}

/* Values Section */
.our-values {
  background: var(--light-gray);
}

.values-header {
  text-align: center;
  margin-bottom: 4rem;
}

.values-header h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 1rem;
}

.values-header p {
  font-size: 1.125rem;
  color: var(--text-light);
}

.values-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
}

.value-card {
  background: var(--white);
  padding: 2.5rem 2rem;
  border-radius: 1.5rem;
  text-align: center;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  border: 1px solid var(--border-light);
}

.value-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
}

.value-icon {
  width: 4rem;
  height: 4rem;
  background: var(--gradient-primary);
  border-radius: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 1.5rem;
  color: var(--dark-gray);
}

.value-card h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 1rem;
}

.value-card p {
  color: var(--text-light);
  line-height: 1.6;
}

/* Team Section */
.our-team {
  background: var(--white);
}

.team-header {
  text-align: center;
  margin-bottom: 4rem;
}

.team-header h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 1rem;
}

.team-header p {
  font-size: 1.125rem;
  color: var(--text-light);
}

.team-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.team-card {
  background: var(--white);
  border-radius: 1.5rem;
  overflow: hidden;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  border: 1px solid var(--border-light);
}

.team-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
}

.team-image {
  height: 250px;
  overflow: hidden;
}

.team-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.team-card:hover .team-image img {
  transform: scale(1.05);
}

.team-info {
  padding: 2rem;
  text-align: center;
}

.team-info h4 {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 0.5rem;
}

.position {
  color: var(--sage-green);
  font-weight: 600;
  margin-bottom: 1rem;
  font-size: 0.875rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.bio {
  color: var(--text-light);
  line-height: 1.6;
  font-size: 0.875rem;
}

/* CTA Section */
.about-cta {
  background: var(--gradient-primary);
  text-align: center;
}

.cta-content h2 {
  font-size: clamp(2rem, 4vw, 2.75rem);
  font-weight: 800;
  margin-bottom: 1.25rem;
  color: var(--dark-gray);
}

.cta-content p {
  font-size: 1.25rem;
  margin-bottom: 2.5rem;
  color: var(--dark-gray);
  opacity: 0.9;
}

.cta-buttons {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .about-hero-content,
  .story-content {
    grid-template-columns: 1fr;
    gap: 3rem;
  }
  
  .hero-stats {
    justify-content: center;
  }
}

@media (max-width: 768px) {
  .about-hero {
    padding: 6rem 0 3rem;
  }
  
  .hero-stats {
    gap: 1.5rem;
  }
  
  .timeline::before {
    left: 0.75rem;
  }
  
  .timeline-item {
    padding-left: 2.5rem;
  }
  
  .timeline-year {
    width: 1.5rem;
    height: 1.5rem;
    font-size: 0.625rem;
  }
  
  .values-grid,
  .team-grid {
    grid-template-columns: 1fr;
  }
  
  .value-card {
    padding: 2rem 1.5rem;
  }
  
  .cta-buttons {
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 480px) {
  .about-hero {
    padding: 5rem 0 2rem;
  }
  
  .hero-image img {
    height: 300px;
  }
  
  .story-highlights {
    margin-top: 2rem;
  }
  
  .highlight {
    margin-bottom: 1.5rem;
  }
  
  .highlight-icon {
    width: 2.5rem;
    height: 2.5rem;
  }
  
  .value-card {
    padding: 1.5rem 1rem;
  }
  
  .team-info {
    padding: 1.5rem;
  }
}