/* Loading Styles */
.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 60vh;
  text-align: center;
}

.loading-spinner {
  width: 3rem;
  height: 3rem;
  border: 3px solid var(--border-light);
  border-top: 3px solid var(--sage-green);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Blogs Hero Section */
.blogs-hero {
  background: var(--gradient-primary);
  padding: 8rem 0 4rem;
  text-align: center;
}

.blogs-hero-content .section-tag {
  display: inline-block;
  background: var(--sage-green);
  color: var(--white);
  padding: 0.5rem 1rem;
  border-radius: 1.25rem;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin-bottom: 1.5rem;
}

.blogs-hero-content h1 {
  font-size: clamp(2.5rem, 5vw, 3.5rem);
  font-weight: 900;
  color: var(--dark-gray);
  margin-bottom: 1.5rem;
  line-height: 1.1;
}

.blogs-hero-content p {
  font-size: clamp(1.125rem, 2vw, 1.375rem);
  color: var(--dark-gray);
  opacity: 0.8;
  max-width: 800px;
  margin: 0 auto 3rem;
  line-height: 1.6;
}

.hero-features {
  display: flex;
  justify-content: center;
  gap: 3rem;
  flex-wrap: wrap;
}

.hero-feature {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-weight: 600;
  color: var(--dark-gray);
}

.feature-icon {
  width: 2.5rem;
  height: 2.5rem;
  background: var(--sage-green);
  border-radius: 0.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--white);
}

/* Blogs Main Section */
.blogs-main {
  background: var(--light-gray);
  padding: 5rem 0;
}

.no-blogs {
  text-align: center;
  padding: 4rem 0;
  max-width: 600px;
  margin: 0 auto;
}

.no-blogs h3 {
  font-size: 1.5rem;
  color: var(--dark-gray);
  margin-bottom: 1rem;
  font-weight: 600;
}

.no-blogs p {
  color: var(--text-light);
  font-size: 1rem;
  line-height: 1.6;
}

/* Fixed Blogs Grid Layout */
.blogs-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(380px, 1fr));
  gap: 2rem;
  justify-content: center;
}

/* Ensure proper spacing and no empty space */
@media (min-width: 1200px) {
  .blogs-grid {
    grid-template-columns: repeat(auto-fit, minmax(380px, 1fr));
    max-width: 1200px;
    margin: 0 auto;
  }
}

.blog-card {
  background: var(--white);
  border-radius: 1.5rem;
  overflow: hidden;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid var(--border-light);
  cursor: pointer;
  height: fit-content;
  display: flex;
  flex-direction: column;
  min-height: 450px;
}

.blog-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
}

.blog-image {
  height: 250px;
  position: relative;
  overflow: hidden;
}

.blog-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: transform 0.3s ease;
}

.blog-card:hover .blog-image img {
  transform: scale(1.05);
}

.blog-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(99, 116, 100, 0.8), rgba(42, 42, 42, 0.6));
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.blog-card:hover .blog-overlay {
  opacity: 1;
}

.read-more-icon {
  color: var(--white);
  opacity: 0.9;
}

.blog-content {
  padding: 2rem;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.blog-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.blog-date {
  color: var(--text-light);
  font-size: 0.875rem;
  font-weight: 500;
}

.blog-category {
  background: var(--primary-green);
  color: var(--dark-gray);
  padding: 0.25rem 0.75rem;
  border-radius: 1rem;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.blog-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 1rem;
  line-height: 1.3;
}

.blog-excerpt {
  color: var(--text-light);
  line-height: 1.6;
  margin-bottom: 1.5rem;
  flex: 1;
}

.blog-footer {
  margin-top: auto;
  padding-top: 1rem;
  border-top: 1px solid var(--border-light);
}

.read-more-link {
  color: var(--sage-green);
  font-weight: 600;
  font-size: 0.875rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

/* Blog Modal Styles */
.blog-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 2rem;
  backdrop-filter: blur(10px);
  overflow-y: auto;
}

.blog-modal-content {
  background: var(--white);
  border-radius: 1.5rem;
  overflow: hidden;
  max-width: 900px;
  max-height: 90vh;
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.modal-close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: rgba(0, 0, 0, 0.5);
  border: none;
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--white);
  cursor: pointer;
  z-index: 10;
  transition: background-color 0.3s ease;
}

.modal-close:hover {
  background: rgba(0, 0, 0, 0.7);
}

.modal-image {
  height: 300px;
  overflow: hidden;
}

.modal-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.modal-body {
  padding: 2rem;
  flex: 1;
}

.modal-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid var(--border-light);
}

.modal-date {
  color: var(--text-light);
  font-size: 0.875rem;
  font-weight: 500;
}

.modal-category {
  background: var(--sage-green);
  color: var(--white);
  padding: 0.5rem 1rem;
  border-radius: 1.25rem;
  font-size: 0.875rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.modal-title {
  font-size: 2rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 2rem;
  line-height: 1.2;
}

.modal-content {
  line-height: 1.7;
  color: var(--dark-gray);
}

.blog-section-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--sage-green);
  margin: 2rem 0 1rem;
}

.blog-paragraph {
  margin-bottom: 1rem;
  color: var(--text-light);
}

.modal-footer {
  margin-top: 2rem;
  padding-top: 2rem;
  border-top: 1px solid var(--border-light);
}

.share-buttons {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.share-buttons span {
  color: var(--text-light);
  font-size: 0.875rem;
  font-weight: 500;
}

.share-btn {
  background: var(--light-gray);
  border: 1px solid var(--border-light);
  border-radius: 0.5rem;
  width: 2.5rem;
  height: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--sage-green);
  cursor: pointer;
  transition: all 0.3s ease;
}

.share-btn:hover {
  background: var(--sage-green);
  color: var(--white);
}

/* CTA Section */
.blogs-cta {
  background: var(--gradient-dark);
  color: var(--white);
  text-align: center;
  padding: 5rem 0;
}

.blogs-cta .cta-content h2 {
  font-size: clamp(2rem, 4vw, 2.75rem);
  font-weight: 800;
  margin-bottom: 1.25rem;
  color: var(--white);
}

.blogs-cta .cta-content p {
  font-size: 1.25rem;
  margin-bottom: 2.5rem;
  color: rgba(255, 255, 255, 0.9);
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;
}

.cta-buttons {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .blogs-grid {
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 1.5rem;
  }
  
  .blog-modal-content {
    max-width: 700px;
  }
  
  .modal-image {
    height: 250px;
  }
}

@media (max-width: 768px) {
  .blogs-hero {
    padding: 6rem 0 3rem;
  }
  
  .hero-features {
    gap: 2rem;
    flex-direction: column;
    align-items: center;
  }
  
  .hero-feature {
    gap: 0.5rem;
  }
  
  .feature-icon {
    width: 2rem;
    height: 2rem;
  }
  
  .blogs-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .blog-content {
    padding: 1.5rem;
  }
  
  .blog-title {
    font-size: 1.25rem;
  }
  
  .blog-modal-overlay {
    padding: 1rem;
  }
  
  .modal-body {
    padding: 1.5rem;
  }
  
  .modal-title {
    font-size: 1.5rem;
  }
  
  .modal-meta {
    flex-direction: column;
    gap: 1rem;
    align-items: flex-start;
  }
  
  .share-buttons {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.75rem;
  }
  
  .cta-buttons {
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 480px) {
  .blogs-hero {
    padding: 5rem 0 2rem;
  }
  
  .hero-features {
    gap: 1.5rem;
  }
  
  .hero-feature {
    flex-direction: column;
    text-align: center;
    gap: 0.5rem;
  }
  
  .feature-icon {
    width: 2.5rem;
    height: 2.5rem;
  }
  
  .blog-content {
    padding: 1.25rem;
  }
  
  .blog-title {
    font-size: 1.125rem;
  }
  
  .blog-meta {
    flex-direction: column;
    gap: 0.75rem;
    align-items: flex-start;
  }
  
  .modal-image {
    height: 200px;
  }
  
  .modal-body {
    padding: 1rem;
  }
  
  .modal-title {
    font-size: 1.25rem;
  }
  
  .modal-close {
    width: 2.5rem;
    height: 2.5rem;
  }
  
  .blog-section-title {
    font-size: 1.125rem;
  }
}