/* Gallery Hero Section */
.gallery-hero {
  background: var(--gradient-primary);
  padding: 8rem 0 4rem;
  text-align: center;
}

.gallery-hero-content .section-tag {
  display: inline-block;
  background: var(--sage-green);
  color: var(--white);
  padding: 0.5rem 1rem;
  border-radius: 1.25rem;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin-bottom: 1.5rem;
}

.gallery-hero-content h1 {
  font-size: clamp(2.5rem, 5vw, 3.5rem);
  font-weight: 900;
  color: var(--dark-gray);
  margin-bottom: 1.5rem;
  line-height: 1.1;
}

.gallery-hero-content p {
  font-size: clamp(1.125rem, 2vw, 1.375rem);
  color: var(--dark-gray);
  opacity: 0.8;
  max-width: 800px;
  margin: 0 auto 3rem;
  line-height: 1.6;
}

.gallery-stats {
  display: flex;
  justify-content: center;
  gap: clamp(2rem, 8vw, 4rem);
  flex-wrap: wrap;
}

.gallery-stat {
  text-align: center;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 1.5rem 2rem;
  border-radius: 1rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.stat-number {
  display: block;
  font-size: clamp(2rem, 4vw, 3rem);
  font-weight: 900;
  color: var(--dark-gray);
  line-height: 1;
  margin-bottom: 0.5rem;
}

.stat-label {
  font-size: clamp(0.875rem, 1.5vw, 1rem);
  color: var(--dark-gray);
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  opacity: 0.8;
}

/* Gallery Filter Section */
.gallery-filter-section {
  background: var(--white);
  padding: 2rem 0;
  border-bottom: 1px solid var(--border-light);
  position: sticky;
  top: 80px;
  z-index: 100;
}

.gallery-filters {
  display: flex;
  justify-content: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.filter-btn {
  background: transparent;
  border: 2px solid var(--border-light);
  color: var(--text-light);
  padding: 0.75rem 1.5rem;
  border-radius: 2rem;
  font-weight: 600;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.filter-btn:hover {
  border-color: var(--sage-green);
  color: var(--sage-green);
}

.filter-btn.active {
  background: var(--sage-green);
  border-color: var(--sage-green);
  color: var(--white);
}

/* Gallery Main Section */
.gallery-main {
  background: var(--light-gray);
  padding: 4rem 0;
}

.gallery-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 2rem;
}

.gallery-item {
  background: var(--white);
  border-radius: 1.5rem;
  overflow: hidden;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid var(--border-light);
  cursor: pointer;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.gallery-item:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
}

.gallery-image {
  height: 250px;
  position: relative;
  overflow: hidden;
}

.gallery-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: transform 0.3s ease;
}

.gallery-item:hover .gallery-image img {
  transform: scale(1.05);
}

.gallery-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(99, 116, 100, 0.8), rgba(42, 42, 42, 0.6));
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2rem;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.gallery-item:hover .gallery-overlay {
  opacity: 1;
}

.gallery-info {
  color: var(--white);
}

.gallery-info h3 {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.gallery-info p {
  font-size: 1rem;
  margin-bottom: 0.5rem;
  opacity: 0.9;
}

.gallery-size {
  background: var(--primary-green);
  color: var(--dark-gray);
  padding: 0.25rem 0.75rem;
  border-radius: 1rem;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.gallery-icon {
  color: var(--white);
  opacity: 0.8;
}

.gallery-content {
  padding: 1.5rem;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.gallery-content h4 {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 0.5rem;
}

.gallery-location {
  color: var(--sage-green);
  font-weight: 600;
  font-size: 0.875rem;
  margin-bottom: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.gallery-description {
  color: var(--text-light);
  line-height: 1.6;
  margin-bottom: 1rem;
  flex: 1;
  font-size: 0.875rem;
}

.gallery-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 1rem;
  border-top: 1px solid var(--border-light);
}

.gallery-category {
  background: var(--light-gray);
  color: var(--sage-green);
  padding: 0.25rem 0.75rem;
  border-radius: 1rem;
  font-size: 0.75rem;
  font-weight: 500;
  border: 1px solid var(--sage-green);
}

.gallery-size-meta {
  color: var(--text-light);
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

/* Lightbox Styles */
.lightbox-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 2rem;
  backdrop-filter: blur(10px);
}

.lightbox-content {
  background: var(--white);
  border-radius: 1.5rem;
  overflow: hidden;
  max-width: 900px;
  max-height: 90vh;
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
}

.lightbox-close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: rgba(0, 0, 0, 0.5);
  border: none;
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--white);
  cursor: pointer;
  z-index: 10;
  transition: background-color 0.3s ease;
}

.lightbox-close:hover {
  background: rgba(0, 0, 0, 0.7);
}

.lightbox-image {
  height: 400px;
  overflow: hidden;
}

.lightbox-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.lightbox-info {
  padding: 2rem;
}

.lightbox-info h3 {
  font-size: 2rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 0.5rem;
}

.lightbox-location {
  color: var(--sage-green);
  font-weight: 600;
  font-size: 1rem;
  margin-bottom: 1rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.lightbox-description {
  color: var(--text-light);
  line-height: 1.6;
  margin-bottom: 1.5rem;
  font-size: 1rem;
}

.lightbox-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 1rem;
  border-top: 1px solid var(--border-light);
}

.lightbox-category {
  background: var(--sage-green);
  color: var(--white);
  padding: 0.5rem 1rem;
  border-radius: 1.25rem;
  font-size: 0.875rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.lightbox-size {
  color: var(--text-light);
  font-size: 0.875rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

/* CTA Section */
.gallery-cta {
  background: var(--gradient-dark);
  color: var(--white);
  text-align: center;
  padding: 5rem 0;
}

.gallery-cta .cta-content h2 {
  font-size: clamp(2rem, 4vw, 2.75rem);
  font-weight: 800;
  margin-bottom: 1.25rem;
  color: var(--white);
}

.gallery-cta .cta-content p {
  font-size: 1.25rem;
  margin-bottom: 2.5rem;
  color: rgba(255, 255, 255, 0.9);
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;
}

.cta-buttons {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .gallery-grid {
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  }
  
  .lightbox-content {
    max-width: 700px;
  }
  
  .lightbox-image {
    height: 300px;
  }
}

@media (max-width: 768px) {
  .gallery-hero {
    padding: 6rem 0 3rem;
  }
  
  .gallery-stats {
    gap: 2rem;
    flex-direction: column;
    align-items: center;
  }
  
  .gallery-stat {
    padding: 1rem 1.5rem;
  }
  
  .gallery-filters {
    gap: 0.75rem;
  }
  
  .filter-btn {
    padding: 0.625rem 1.25rem;
    font-size: 0.8rem;
  }
  
  .gallery-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .gallery-content {
    padding: 1.25rem;
  }
  
  .gallery-overlay {
    padding: 1.5rem;
  }
  
  .gallery-info h3 {
    font-size: 1.25rem;
  }
  
  .lightbox-overlay {
    padding: 1rem;
  }
  
  .lightbox-info {
    padding: 1.5rem;
  }
  
  .lightbox-info h3 {
    font-size: 1.5rem;
  }
  
  .lightbox-meta {
    flex-direction: column;
    gap: 1rem;
    align-items: flex-start;
  }
  
  .cta-buttons {
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 480px) {
  .gallery-hero {
    padding: 5rem 0 2rem;
  }
  
  .gallery-stats {
    gap: 1.5rem;
  }
  
  .gallery-stat {
    padding: 1rem;
    width: 100%;
    max-width: 200px;
  }
  
  .gallery-filters {
    gap: 0.5rem;
  }
  
  .filter-btn {
    padding: 0.5rem 1rem;
    font-size: 0.75rem;
  }
  
  .gallery-content {
    padding: 1rem;
  }
  
  .gallery-overlay {
    padding: 1rem;
    flex-direction: column;
    text-align: center;
    gap: 1rem;
  }
  
  .lightbox-image {
    height: 250px;
  }
  
  .lightbox-info {
    padding: 1rem;
  }
  
  .lightbox-info h3 {
    font-size: 1.25rem;
  }
  
  .lightbox-close {
    width: 2.5rem;
    height: 2.5rem;
  }
}