/* Services Hero Section */
.services-hero {
  background: var(--gradient-primary);
  padding: 8rem 0 4rem;
  text-align: center;
}

.services-hero-content .section-tag {
  display: inline-block;
  background: var(--sage-green);
  color: var(--white);
  padding: 0.5rem 1rem;
  border-radius: 1.25rem;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin-bottom: 1.5rem;
}

.services-hero-content h1 {
  font-size: clamp(2.5rem, 5vw, 3.5rem);
  font-weight: 900;
  color: var(--dark-gray);
  margin-bottom: 1.5rem;
  line-height: 1.1;
}

.services-hero-content p {
  font-size: clamp(1.125rem, 2vw, 1.375rem);
  color: var(--dark-gray);
  opacity: 0.8;
  max-width: 700px;
  margin: 0 auto 3rem;
  line-height: 1.6;
}

.hero-features {
  display: flex;
  justify-content: center;
  gap: 3rem;
  flex-wrap: wrap;
}

.hero-feature {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-weight: 600;
  color: var(--dark-gray);
}

.feature-icon {
  width: 2.5rem;
  height: 2.5rem;
  background: var(--sage-green);
  border-radius: 0.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--white);
}

/* Services Main Section */
.services-main {
  background: var(--light-gray);
  padding: 5rem 0;
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(380px, 1fr));
  gap: 2rem;
}

.service-card {
  background: var(--white);
  border-radius: 1.5rem;
  overflow: hidden;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid var(--border-light);
  height: 100%;
  display: flex;
  flex-direction: column;
}

.service-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
}

.service-image {
  height: 200px;
  position: relative;
  overflow: hidden;
}

.service-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: transform 0.3s ease;
}

.service-card:hover .service-image img {
  transform: scale(1.05);
}

.service-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(99, 116, 100, 0.8), rgba(42, 42, 42, 0.6));
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.service-card:hover .service-overlay {
  opacity: 1;
}

.service-overlay .service-icon {
  color: var(--white);
}

.service-content {
  padding: 2rem;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.service-content h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 1rem;
}

.service-description {
  color: var(--text-light);
  line-height: 1.6;
  margin-bottom: 1.5rem;
  flex: 1;
}

.service-features {
  margin-bottom: 1.5rem;
}

.service-features h4 {
  font-size: 1rem;
  font-weight: 600;
  color: var(--dark-gray);
  margin-bottom: 0.75rem;
}

.service-features ul {
  list-style: none;
}

.service-features ul li {
  padding: 0.25rem 0;
  color: var(--text-light);
  position: relative;
  padding-left: 1.5rem;
  font-size: 0.875rem;
}

.service-features ul li:before {
  content: "●";
  position: absolute;
  left: 0;
  color: var(--sage-green);
  font-weight: bold;
}

.service-tech {
  margin-bottom: 2rem;
}

.service-tech h4 {
  font-size: 1rem;
  font-weight: 600;
  color: var(--dark-gray);
  margin-bottom: 0.75rem;
}

.tech-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.tech-tag {
  background: var(--primary-green);
  color: var(--dark-gray);
  padding: 0.25rem 0.75rem;
  border-radius: 1rem;
  font-size: 0.75rem;
  font-weight: 500;
}

.service-action {
  margin-top: auto;
}

.service-action .btn {
  width: 100%;
  justify-content: center;
  gap: 0.5rem;
}

/* Process Section */
.our-process {
  background: var(--white);
  padding: 5rem 0;
}

.process-header {
  text-align: center;
  margin-bottom: 4rem;
}

.process-header h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 1rem;
}

.process-header p {
  font-size: 1.125rem;
  color: var(--text-light);
}

.process-steps {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.process-step {
  text-align: center;
  position: relative;
}

.step-number {
  width: 4rem;
  height: 4rem;
  background: var(--gradient-primary);
  color: var(--dark-gray);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  font-weight: 900;
  margin: 0 auto 1.5rem;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}

.step-content h4 {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--dark-gray);
  margin-bottom: 0.75rem;
}

.step-content p {
  color: var(--text-light);
  line-height: 1.6;
  font-size: 0.875rem;
}

/* Why Choose Section */
.why-choose-services {
  background: var(--light-gray);
  padding: 5rem 0;
}

.why-choose-content {
  display: grid;
  grid-template-columns: 1.5fr 1fr;
  gap: 4rem;
  align-items: center;
}

.why-choose-text h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 1.5rem;
}

.why-choose-text > p {
  font-size: 1.125rem;
  color: var(--text-light);
  margin-bottom: 2rem;
  line-height: 1.6;
}

.choose-points {
  margin-bottom: 2.5rem;
}

.choose-point {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.point-icon {
  width: 2rem;
  height: 2rem;
  background: var(--sage-green);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--white);
  flex-shrink: 0;
  margin-top: 0.25rem;
}

.choose-point h4 {
  font-size: 1.125rem;
  font-weight: 600;
  color: var(--dark-gray);
  margin-bottom: 0.5rem;
}

.choose-point p {
  color: var(--text-light);
  font-size: 0.875rem;
  line-height: 1.5;
  margin: 0;
}

.why-choose-stats {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
}

.stat-card {
  background: var(--white);
  padding: 2rem 1.5rem;
  border-radius: 1rem;
  text-align: center;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  border: 1px solid var(--border-light);
}

.stat-numberr {
  font-size: 2.5rem;
  font-weight: 900;
  color: var(--sage-green);
  display: block;
  line-height: 1;
  margin-bottom: 0.5rem;
}

.stat-labell {
  font-size: 0.875rem;
  color: var(--text-light);
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

/* CTA Section */
.services-cta {
  background: var(--gradient-dark);
  color: var(--white);
  text-align: center;
  padding: 5rem 0;
}

.services-cta .cta-content h2 {
  font-size: clamp(2rem, 4vw, 2.75rem);
  font-weight: 800;
  margin-bottom: 1.25rem;
  color: var(--white);
}

.services-cta .cta-content p {
  font-size: 1.25rem;
  margin-bottom: 2.5rem;
  color: rgba(255, 255, 255, 0.9);
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.cta-buttons {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .services-grid {
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  }
  
  .why-choose-content {
    grid-template-columns: 1fr;
    gap: 3rem;
  }
  
  .why-choose-stats {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }
}

@media (max-width: 768px) {
  .services-hero {
    padding: 6rem 0 3rem;
  }
  
  .hero-features {
    gap: 2rem;
    flex-direction: column;
    align-items: center;
  }
  
  .services-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .service-content {
    padding: 1.5rem;
  }
  
  .process-steps {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  
  .step-number {
    width: 3rem;
    height: 3rem;
    font-size: 1.25rem;
  }
  
  .choose-points {
    margin-bottom: 2rem;
  }
  
  .choose-point {
    margin-bottom: 1.25rem;
  }
  
  .why-choose-stats {
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }
  
  .stat-card {
    padding: 1.5rem 1rem;
  }
  
  .cta-buttons {
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 480px) {
  .services-hero {
    padding: 5rem 0 2rem;
  }
  
  .hero-features {
    gap: 1.5rem;
  }
  
  .hero-feature {
    flex-direction: column;
    text-align: center;
    gap: 0.5rem;
  }
  
  .feature-icon {
    width: 2rem;
    height: 2rem;
  }
  
  .service-content {
    padding: 1.25rem;
  }
  
  .service-content h3 {
    font-size: 1.25rem;
  }
  
  .tech-tags {
    gap: 0.375rem;
  }
  
  .tech-tag {
    padding: 0.25rem 0.5rem;
    font-size: 0.675rem;
  }
  
  .why-choose-stats {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .stat-number {
    font-size: 2rem;
  }
  
  .choose-point {
    flex-direction: column;
    text-align: center;
    gap: 0.75rem;
  }
  
  .point-icon {
    margin: 0 auto;
  }
}