/* Industries Hero Section */
.industries-hero {
  background: var(--gradient-primary);
  padding: 8rem 0 4rem;
  text-align: center;
}

.industries-hero-content .section-tag {
  display: inline-block;
  background: var(--sage-green);
  color: var(--white);
  padding: 0.5rem 1rem;
  border-radius: 1.25rem;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin-bottom: 1.5rem;
}

.industries-hero-content h1 {
  font-size: clamp(2.5rem, 5vw, 3.5rem);
  font-weight: 900;
  color: var(--dark-gray);
  margin-bottom: 1.5rem;
  line-height: 1.1;
}

.industries-hero-content p {
  font-size: clamp(1.125rem, 2vw, 1.375rem);
  color: var(--dark-gray);
  opacity: 0.8;
  max-width: 800px;
  margin: 0 auto 3rem;
  line-height: 1.6;
}

.hero-stats {
  display: flex;
  justify-content: center;
  gap: clamp(2rem, 8vw, 4rem);
  flex-wrap: wrap;
}

.hero-stat {
  text-align: center;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 1.5rem 2rem;
  border-radius: 1rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.stat-number {
  display: block;
  font-size: clamp(2rem, 4vw, 3rem);
  font-weight: 900;
  color: var(--dark-gray);
  line-height: 1;
  margin-bottom: 0.5rem;
}

.stat-label {
  font-size: clamp(0.875rem, 1.5vw, 1rem);
  color: var(--dark-gray);
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  opacity: 0.8;
}

/* Industries Main Section */
.industries-main {
  background: var(--light-gray);
  padding: 5rem 0;
}

.industries-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 2rem;
}

.industry-card {
  background: var(--white);
  border-radius: 1.5rem;
  overflow: hidden;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid var(--border-light);
  height: 100%;
  display: flex;
  flex-direction: column;
}

.industry-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
}

.industry-image {
  height: 200px;
  position: relative;
  overflow: hidden;
}

.industry-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: transform 0.3s ease;
}

.industry-card:hover .industry-image img {
  transform: scale(1.05);
}

.industry-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(99, 116, 100, 0.8), rgba(42, 42, 42, 0.6));
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.industry-card:hover .industry-overlay {
  opacity: 1;
}

.industry-overlay .industry-icon {
  color: var(--white);
}

.industry-content {
  padding: 2rem;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.industry-content h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 1rem;
}

.industry-description {
  color: var(--text-light);
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.industry-details {
  flex: 1;
  margin-bottom: 2rem;
}

.detail-section {
  margin-bottom: 1.5rem;
}

.detail-section h4 {
  font-size: 1rem;
  font-weight: 600;
  color: var(--dark-gray);
  margin-bottom: 0.75rem;
}

.challenges-list,
.solutions-list {
  list-style: none;
}

.challenges-list li,
.solutions-list li {
  padding: 0.25rem 0;
  color: var(--text-light);
  position: relative;
  padding-left: 1.5rem;
  font-size: 0.875rem;
}

.challenges-list li:before {
  content: "⚠";
  position: absolute;
  left: 0;
  color: #f59e0b;
  font-weight: bold;
}

.solutions-list li:before {
  content: "✓";
  position: absolute;
  left: 0;
  color: var(--sage-green);
  font-weight: bold;
}

.clients-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.client-tag {
  background: var(--primary-green);
  color: var(--dark-gray);
  padding: 0.25rem 0.75rem;
  border-radius: 1rem;
  font-size: 0.75rem;
  font-weight: 500;
}

.industry-action {
  margin-top: auto;
}

.industry-action .btn {
  width: 100%;
  justify-content: center;
  gap: 0.5rem;
}

/* Industry Expertise Section */
.industry-expertise {
  background: var(--white);
  padding: 5rem 0;
}

.expertise-content {
  display: grid;
  grid-template-columns: 1.5fr 1fr;
  gap: 4rem;
  align-items: center;
}

.expertise-text h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 1.5rem;
}

.expertise-text > p {
  font-size: 1.125rem;
  color: var(--text-light);
  margin-bottom: 2.5rem;
  line-height: 1.6;
}

.expertise-points {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.expertise-point {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
}

.point-icon {
  width: 3rem;
  height: 3rem;
  background: var(--sage-green);
  border-radius: 0.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--white);
  flex-shrink: 0;
}

.expertise-point h4 {
  font-size: 1.125rem;
  font-weight: 600;
  color: var(--dark-gray);
  margin-bottom: 0.5rem;
}

.expertise-point p {
  color: var(--text-light);
  font-size: 0.875rem;
  line-height: 1.5;
  margin: 0;
}

.expertise-image img {
  width: 100%;
  height: 400px;
  object-fit: cover;
  border-radius: 1.5rem;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
}

/* Success Stories Section */
.success-stories {
  background: var(--light-gray);
  padding: 5rem 0;
}

.stories-header {
  text-align: center;
  margin-bottom: 4rem;
}

.stories-header h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 1rem;
}

.stories-header p {
  font-size: 1.125rem;
  color: var(--text-light);
}

.stories-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.story-card {
  background: var(--white);
  padding: 2.5rem 2rem;
  border-radius: 1.5rem;
  text-align: center;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  border: 1px solid var(--border-light);
  position: relative;
}

.story-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
}

.story-number {
  width: 3rem;
  height: 3rem;
  background: var(--gradient-primary);
  color: var(--dark-gray);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.25rem;
  font-weight: 900;
  margin: 0 auto 1.5rem;
}

.story-card h4 {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--dark-gray);
  margin-bottom: 1rem;
}

.story-card p {
  color: var(--text-light);
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.story-metrics {
  display: flex;
  justify-content: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.metric {
  background: var(--sage-green);
  color: var(--white);
  padding: 0.5rem 1rem;
  border-radius: 1.25rem;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

/* CTA Section */
.industries-cta {
  background: var(--gradient-dark);
  color: var(--white);
  text-align: center;
  padding: 5rem 0;
}

.industries-cta .cta-content h2 {
  font-size: clamp(2rem, 4vw, 2.75rem);
  font-weight: 800;
  margin-bottom: 1.25rem;
  color: var(--white);
}

.industries-cta .cta-content p {
  font-size: 1.25rem;
  margin-bottom: 2.5rem;
  color: rgba(255, 255, 255, 0.9);
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;
}

.cta-buttons {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .industries-grid {
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  }
  
  .expertise-content {
    grid-template-columns: 1fr;
    gap: 3rem;
  }
  
  .expertise-image {
    order: -1;
  }
}

@media (max-width: 768px) {
  .industries-hero {
    padding: 6rem 0 3rem;
  }
  
  .hero-stats {
    gap: 2rem;
    flex-direction: column;
    align-items: center;
  }
  
  .hero-stat {
    padding: 1rem 1.5rem;
  }
  
  .industries-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .industry-content {
    padding: 1.5rem;
  }
  
  .expertise-points {
    gap: 1.25rem;
  }
  
  .expertise-point {
    gap: 0.75rem;
  }
  
  .point-icon {
    width: 2.5rem;
    height: 2.5rem;
  }
  
  .stories-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .story-card {
    padding: 2rem 1.5rem;
  }
  
  .cta-buttons {
    flex-direction: column;
    align-items: center;
  }
  
  .expertise-image img {
    height: 300px;
  }
}

@media (max-width: 480px) {
  .industries-hero {
    padding: 5rem 0 2rem;
  }
  
  .hero-stats {
    gap: 1.5rem;
  }
  
  .hero-stat {
    padding: 1rem;
    width: 100%;
    max-width: 200px;
  }
  
  .industries-grid {
    grid-template-columns: 1fr;
  }
  
  .industry-content {
    padding: 1.25rem;
  }
  
  .industry-content h3 {
    font-size: 1.25rem;
  }
  
  .clients-tags {
    gap: 0.375rem;
  }
  
  .client-tag {
    padding: 0.25rem 0.5rem;
    font-size: 0.675rem;
  }
  
  .story-card {
    padding: 1.5rem 1rem;
  }
  
  .story-number {
    width: 2.5rem;
    height: 2.5rem;
    font-size: 1rem;
  }
  
  .story-metrics {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .metric {
    padding: 0.375rem 0.75rem;
    font-size: 0.675rem;
  }
  
  .expertise-point {
    flex-direction: column;
    text-align: center;
    gap: 0.5rem;
  }
  
  .point-icon {
    margin: 0 auto;
  }
}