/* Brands Hero Section */
.brands-hero {
  background: var(--gradient-primary);
  padding: 8rem 0 4rem;
  text-align: center;
}

.brands-hero-content .section-tag {
  display: inline-block;
  background: var(--sage-green);
  color: var(--white);
  padding: 0.5rem 1rem;
  border-radius: 1.25rem;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin-bottom: 1.5rem;
}

.brands-hero-content h1 {
  font-size: clamp(2.5rem, 5vw, 3.5rem);
  font-weight: 900;
  color: var(--dark-gray);
  margin-bottom: 1.5rem;
  line-height: 1.1;
}

.brands-hero-content p {
  font-size: clamp(1.125rem, 2vw, 1.375rem);
  color: var(--dark-gray);
  opacity: 0.8;
  max-width: 800px;
  margin: 0 auto 3rem;
  line-height: 1.6;
}

.partnership-levels {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
}

.partnership-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 1rem;
  padding: 2rem;
  text-align: left;
  transition: all 0.3s ease;
}

.partnership-card:hover {
  transform: translateY(-4px);
  background: rgba(255, 255, 255, 0.15);
}

.partnership-icon {
  width: 3rem;
  height: 3rem;
  background: var(--sage-green);
  border-radius: 0.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--white);
  margin-bottom: 1rem;
}

.partnership-info h3 {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 0.5rem;
}

.partnership-info p {
  color: var(--dark-gray);
  opacity: 0.8;
  font-size: 0.875rem;
  line-height: 1.5;
  margin-bottom: 1rem;
}

.partner-count {
  background: var(--sage-green);
  color: var(--white);
  padding: 0.25rem 0.75rem;
  border-radius: 1rem;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

/* Brand Categories Section */
.brand-categories {
  background: var(--light-gray);
  padding: 5rem 0;
}

.category-section {
  margin-bottom: 4rem;
}

.category-section:last-child {
  margin-bottom: 0;
}

.category-header {
  text-align: center;
  margin-bottom: 3rem;
}

.category-header h2 {
  font-size: 2rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 0.75rem;
}

.category-header p {
  font-size: 1.125rem;
  color: var(--text-light);
}

.brands-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
}

.brand-card {
  background: var(--white);
  border-radius: 1.5rem;
  overflow: hidden;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid var(--border-light);
  height: 100%;
  display: flex;
  flex-direction: column;
}

.brand-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
}

.brand-image {
  height: 180px;
  position: relative;
  overflow: hidden;
}

.brand-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: transform 0.3s ease;
}

.brand-card:hover .brand-image img {
  transform: scale(1.05);
}

.brand-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(99, 116, 100, 0.8), rgba(42, 42, 42, 0.6));
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.brand-card:hover .brand-overlay {
  opacity: 1;
}

.partnership-badge {
  background: var(--primary-green);
  color: var(--dark-gray);
  padding: 0.5rem 1rem;
  border-radius: 1.25rem;
  font-size: 0.875rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.brand-content {
  padding: 2rem;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.brand-content h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 1rem;
}

.brand-description {
  color: var(--text-light);
  line-height: 1.6;
  margin-bottom: 1.5rem;
  flex: 1;
}

.brand-products {
  margin-bottom: 1.5rem;
}

.brand-products h4 {
  font-size: 1rem;
  font-weight: 600;
  color: var(--dark-gray);
  margin-bottom: 0.75rem;
}

.product-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.product-tag {
  background: var(--light-gray);
  color: var(--sage-green);
  padding: 0.25rem 0.75rem;
  border-radius: 1rem;
  font-size: 0.75rem;
  font-weight: 500;
  border: 1px solid var(--sage-green);
}

.brand-partnership {
  margin-top: auto;
  padding-top: 1rem;
  border-top: 1px solid var(--border-light);
}

.partnership-level {
  background: var(--sage-green);
  color: var(--white);
  padding: 0.5rem 1rem;
  border-radius: 1.25rem;
  font-size: 0.875rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

/* Partnership Benefits Section */
.partnership-benefits {
  background: var(--white);
  padding: 5rem 0;
}

.benefits-content {
  display: grid;
  grid-template-columns: 1.5fr 1fr;
  gap: 4rem;
  align-items: center;
}

.benefits-text h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 1.5rem;
}

.benefits-text > p {
  font-size: 1.125rem;
  color: var(--text-light);
  margin-bottom: 2.5rem;
  line-height: 1.6;
}

.benefits-list {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.benefit-item {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
}

.benefit-icon {
  width: 2.5rem;
  height: 2.5rem;
  background: var(--sage-green);
  border-radius: 0.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--white);
  flex-shrink: 0;
}

.benefit-item h4 {
  font-size: 1.125rem;
  font-weight: 600;
  color: var(--dark-gray);
  margin-bottom: 0.5rem;
}

.benefit-item p {
  color: var(--text-light);
  font-size: 0.875rem;
  line-height: 1.5;
  margin: 0;
}

.benefits-stats {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
}

.stat-item {
  background: var(--light-gray);
  padding: 2rem 1.5rem;
  border-radius: 1rem;
  text-align: center;
  border: 1px solid var(--border-light);
}

.stat-numberr {
  font-size: 2.5rem;
  font-weight: 900;
  color: var(--sage-green);
  display: block;
  line-height: 1;
  margin-bottom: 0.5rem;
}

.stat-labell {
  font-size: 0.875rem;
  color: var(--text-light);
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

/* CTA Section */
.brands-cta {
  background: var(--gradient-dark);
  color: var(--white);
  text-align: center;
  padding: 5rem 0;
}

.brands-cta .cta-content h2 {
  font-size: clamp(2rem, 4vw, 2.75rem);
  font-weight: 800;
  margin-bottom: 1.25rem;
  color: var(--white);
}

.brands-cta .cta-content p {
  font-size: 1.25rem;
  margin-bottom: 2.5rem;
  color: rgba(255, 255, 255, 0.9);
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;
}

.cta-buttons {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .partnership-levels {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .brands-grid {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  }
  
  .benefits-content {
    grid-template-columns: 1fr;
    gap: 3rem;
  }
  
  .benefits-stats {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }
}

@media (max-width: 768px) {
  .brands-hero {
    padding: 6rem 0 3rem;
  }
  
  .partnership-card {
    padding: 1.5rem;
    text-align: center;
  }
  
  .partnership-icon {
    margin: 0 auto 1rem;
  }
  
  .brands-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .brand-content {
    padding: 1.5rem;
  }
  
  .category-section {
    margin-bottom: 3rem;
  }
  
  .benefits-list {
    gap: 1.25rem;
  }
  
  .benefit-item {
    gap: 0.75rem;
  }
  
  .benefit-icon {
    width: 2rem;
    height: 2rem;
  }
  
  .benefits-stats {
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }
  
  .stat-item {
    padding: 1.5rem 1rem;
  }
  
  .cta-buttons {
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 480px) {
  .brands-hero {
    padding: 5rem 0 2rem;
  }
  
  .partnership-levels {
    gap: 1rem;
  }
  
  .partnership-card {
    padding: 1.25rem;
  }
  
  .partnership-info h3 {
    font-size: 1.125rem;
  }
  
  .partnership-info p {
    font-size: 0.8rem;
  }
  
  .brand-content {
    padding: 1.25rem;
  }
  
  .brand-content h3 {
    font-size: 1.25rem;
  }
  
  .product-tags {
    gap: 0.375rem;
  }
  
  .product-tag {
    padding: 0.25rem 0.5rem;
    font-size: 0.675rem;
  }
  
  .benefits-stats {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .stat-number {
    font-size: 2rem;
  }
  
  .benefit-item {
    flex-direction: column;
    text-align: center;
    gap: 0.5rem;
  }
  
  .benefit-icon {
    margin: 0 auto;
  }
  
  .category-header h2 {
    font-size: 1.5rem;
  }
  
  .category-header p {
    font-size: 1rem;
  }
}