.contact {
  min-height: 100vh;
}

/* Hero Section */
.contact-hero {
  background: var(--gradient-primary);
  padding: 8rem 0 4rem;
  text-align: center;
}

.contact-hero-content h1 {
  font-size: clamp(2.5rem, 5vw, 3.5rem);
  font-weight: 900;
  color: var(--dark-gray);
  margin-bottom: 1.5rem;
  line-height: 1.1;
}

.contact-hero-content p {
  font-size: clamp(1.125rem, 2vw, 1.375rem);
  color: var(--dark-gray);
  opacity: 0.8;
  max-width: 600px;
  margin: 0 auto;
}

/* Main Contact Section */
.contact-main {
  padding: 5rem 0;
  background: var(--light-gray);
}

.contact-grid {
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  gap: 4rem;
  align-items: start;
}

/* Contact Info */
.contact-info {
  background: var(--white);
  padding: 3rem 2rem;
  border-radius: 1.5rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  height: fit-content;
  position: sticky;
  top: 2rem;
}

.contact-info-header h2 {
  font-size: 2rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 1rem;
}

.contact-info-header p {
  color: var(--text-light);
  margin-bottom: 2.5rem;
  line-height: 1.6;
}

.contact-methods {
  margin-bottom: 3rem;
}

.contact-method {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  margin-bottom: 2rem;
  padding: 1.5rem;
  background: var(--light-gray);
  border-radius: 1rem;
  transition: all 0.3s ease;
}

.contact-method:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.method-icon {
  width: 3rem;
  height: 3rem;
  background: var(--sage-green);
  border-radius: 0.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--white);
  flex-shrink: 0;
}

.method-content h4 {
  font-size: 1.125rem;
  font-weight: 600;
  color: var(--dark-gray);
  margin-bottom: 0.5rem;
}

.method-content p {
  color: var(--dark-gray);
  font-weight: 500;
  margin-bottom: 0.25rem;
}

.method-content span {
  font-size: 0.875rem;
  color: var(--text-light);
}

.contact-cta {
  background: var(--gradient-primary);
  padding: 2rem;
  border-radius: 1rem;
  text-align: center;
}

.contact-cta h3 {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 0.75rem;
}

.contact-cta p {
  color: var(--dark-gray);
  opacity: 0.8;
  margin-bottom: 1.5rem;
  font-size: 0.875rem;
}

/* Contact Form */
.contact-form-section {
  background: var(--white);
  padding: 3rem 2rem;
  border-radius: 1.5rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.form-header h2 {
  font-size: 2rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 1rem;
}

.form-header p {
  color: var(--text-light);
  margin-bottom: 2.5rem;
  line-height: 1.6;
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  font-weight: 600;
  color: var(--dark-gray);
  margin-bottom: 0.5rem;
  font-size: 0.875rem;
}

.required {
  color: #ef4444;
}

.form-group input,
.form-group textarea {
  padding: 1rem;
  border: 2px solid var(--border-light);
  border-radius: 0.75rem;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: var(--white);
  font-family: inherit;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--sage-green);
  box-shadow: 0 0 0 3px rgba(99, 116, 100, 0.1);
}

.form-group input::placeholder,
.form-group textarea::placeholder {
  color: var(--text-light);
}

.form-group textarea {
  resize: vertical;
  min-height: 120px;
  line-height: 1.6;
}

.btn-submit {
  margin-top: 1rem;
  padding: 1rem 2rem;
  font-size: 1.125rem;
  gap: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn-submit:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.loading-spinner {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.status-message {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 1.5rem;
  border-radius: 0.75rem;
  margin-top: 1rem;
  font-weight: 500;
}

.status-message.success {
  background: #d1fae5;
  color: #065f46;
  border: 1px solid #a7f3d0;
}

.status-message.error {
  background: #fee2e2;
  color: #991b1b;
  border: 1px solid #fecaca;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .contact-grid {
    grid-template-columns: 1fr;
    gap: 3rem;
  }
  
  .contact-info {
    position: static;
  }
}

@media (max-width: 768px) {
  .contact-hero {
    padding: 6rem 0 3rem;
  }
  
  .contact-main {
    padding: 3rem 0;
  }
  
  .contact-info,
  .contact-form-section {
    padding: 2rem 1.5rem;
  }
  
  .form-row {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .contact-method {
    padding: 1rem;
  }
  
  .method-icon {
    width: 2.5rem;
    height: 2.5rem;
  }
}

@media (max-width: 480px) {
  .contact-hero {
    padding: 5rem 0 2rem;
  }
  
  .contact-info,
  .contact-form-section {
    padding: 1.5rem 1rem;
  }
  
  .contact-cta {
    padding: 1.5rem;
  }
  
  .form-group input,
  .form-group textarea {
    padding: 0.875rem;
  }
}