/* Hero Section */
.hero {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  overflow: hidden;
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
}

.hero-bg-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(42, 42, 42, 0.7) 0%, rgba(99, 116, 100, 0.8) 100%);
  z-index: 2;
}

.hero-content {
  position: relative;
  z-index: 3;
  text-align: center;
  color: var(--white);
  width: 100%;
}

.hero-title {
  font-size: clamp(2.5rem, 6vw, 4.5rem);
  font-weight: 900;
  margin-bottom: 1.5rem;
  line-height: 1.1;
  letter-spacing: -0.025em;
  color: var(--white);
  text-shadow: 0 2px 4px rgba(0,0,0,0.3);
}

.hero-subtitle {
  font-size: clamp(1.125rem, 2.5vw, 1.5rem);
  margin-bottom: 3rem;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;
  color: rgba(255, 255, 255, 0.95);
  text-shadow: 0 1px 2px rgba(0,0,0,0.3);
}

.hero-stats {
  display: flex;
  justify-content: center;
  gap: clamp(2rem, 8vw, 5rem);
  margin: 4rem 0;
}

.stat {
  text-align: center;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 1.5rem 2rem;
  border-radius: 1rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.stat-number {
  font-size: clamp(2rem, 4vw, 3.5rem);
  font-weight: 900;
  color: white !important;
  margin-bottom: 0.5rem;
  line-height: 1;
}

.stat-label {
  font-size: clamp(0.875rem, 1.5vw, 1rem);
  color: white !important;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.hero-buttons {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

.btn-hero {
  padding: 1rem 2rem;
  font-size: 1.125rem;
  gap: 0.5rem;
  min-width: 200px;
}

.btn-lg {
  padding: 1.125rem 2.25rem;
  font-size: 1.25rem;
  min-width: 220px;
}

/* Animation Classes */
.animate-on-scroll {
  opacity: 0;
  transform: translateY(40px);
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
}

.animate-on-scroll.visible {
  opacity: 1;
  transform: translateY(0);
}

/* Services Section */
.expertise {
  background: var(--white);
  padding: 5rem 0;
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 4rem;
}

.service-card {
  background: var(--white);
  border-radius: 1.25rem;
  overflow: hidden;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid var(--border-light);
  height: 100%;
  display: flex;
  flex-direction: column;
}

.service-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
}

.service-image {
  height: 200px;
  width: 100%;
  overflow: hidden;
  position: relative;
}

.service-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: transform 0.3s ease;
}

.service-card:hover .service-image img {
  transform: scale(1.05);
}

.service-content {
  padding: 2rem 1.5rem;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.service-icon {
  width: 3rem;
  height: 3rem;
  background: var(--gradient-primary);
  border-radius: 0.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.25rem;
  color: var(--dark-gray);
}

.service-content h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 0.75rem;
}

.service-content p {
  margin-bottom: 1.25rem;
  line-height: 1.6;
  color: var(--text-light);
  flex: 1;
}

.service-content ul {
  list-style: none;
  margin-top: auto;
}

.service-content ul li {
  padding: 0.375rem 0;
  color: var(--sage-green);
  position: relative;
  padding-left: 1.5rem;
  font-size: 0.875rem;
  font-weight: 500;
}

.service-content ul li:before {
  content: "●";
  position: absolute;
  left: 0;
  color: var(--sage-green);
  font-weight: bold;
}

/* About Preview Section */
.about-preview {
  background: var(--light-gray);
}

.about-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 80px;
  align-items: center;
}

.section-tag {
  display: inline-block;
  background: var(--sage-green);
  color: var(--white);
  padding: 6px 16px;
  border-radius: 20px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin-bottom: 20px;
}

.about-text h2 {
  font-size: clamp(2rem, 4vw, 2.5rem);
  font-weight: 700;
  color: var(--dark-gray);
  margin-bottom: 24px;
  line-height: 1.2;
}

.about-text p {
  font-size: 1.125rem;
  line-height: 1.7;
  margin-bottom: 32px;
  color: var(--text-light);
}

.about-features {
  margin-bottom: 40px;
}

.feature-item {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
  font-size: 1rem;
  color: var(--dark-gray);
}

.feature-check {
  width: 24px;
  height: 24px;
  background: var(--sage-green);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 16px;
  color: var(--white);
  flex-shrink: 0;
}

.about-image {
  position: relative;
}

.about-image img {
  width: 100%;
  height: 400px;
  object-fit: cover;
  border-radius: 20px;
  box-shadow: var(--shadow-lg);
}

/* Industries Preview */
.industries-preview {
  background: var(--white);
}

.industries-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 24px;
  margin-top: 64px;
}

.industry-card {
  background: var(--white);
  padding: 32px 24px;
  border-radius: 16px;
  text-align: center;
  border: 1px solid var(--border-light);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: var(--shadow-sm);
}

.industry-card:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-lg);
  border-color: var(--sage-green);
}

.industry-icon {
  width: 64px;
  height: 64px;
  background: var(--light-gray);
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 20px;
  color: var(--sage-green);
  transition: all 0.3s ease;
}

.industry-card:hover .industry-icon {
  background: var(--sage-green);
  color: var(--white);
}

.industry-card h4 {
  font-size: 1.125rem;
  font-weight: 600;
  color: var(--dark-gray);
  margin-bottom: 12px;
}

.industry-card p {
  font-size: 0.875rem;
  color: var(--text-light);
  line-height: 1.5;
}

/* Trusted Brands */
.trusted-brands {
  background: var(--light-gray);
}

.brands-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 24px;
  margin-top: 64px;
}

.brand-item {
  background: var(--white);
  padding: 40px 24px;
  border-radius: 16px;
  text-align: center;
  border: 1px solid var(--border-light);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: var(--shadow-sm);
}

.brand-item:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-md);
  border-color: var(--sage-green);
}

.brand-logo {
  font-weight: 600;
  font-size: 1.125rem;
  color: var(--sage-green);
}

/* Testimonials */
.testimonials {
  background: var(--dark-gray);
  color: var(--white);
}

.testimonials .section-title {
  color: var(--white);
}

.testimonials .section-subtitle {
  color: rgba(255, 255, 255, 0.8);
}

.testimonials-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 32px;
  margin-top: 64px;
}

.testimonial-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 40px 32px;
  transition: all 0.3s ease;
}

.testimonial-card:hover {
  transform: translateY(-8px);
  background: rgba(255, 255, 255, 0.08);
}

.testimonial-content {
  position: relative;
}

.quote-icon {
  color: var(--primary-green);
  margin-bottom: 24px;
}

.testimonial-content p {
  font-size: 1.125rem;
  line-height: 1.7;
  margin-bottom: 24px;
  font-style: italic;
  color: rgba(255, 255, 255, 0.9);
}

.testimonial-author {
  display: flex;
  align-items: center;
  gap: 16px;
}

.author-info strong {
  color: var(--primary-green);
  font-size: 1rem;
  font-weight: 600;
}

.author-info span {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.875rem;
  display: block;
  margin-top: 4px;
}

/* CTA Section */
.cta {
  background: var(--gradient-primary);
  color: var(--dark-gray);
  text-align: center;
}

.cta-content h2 {
  font-size: clamp(2rem, 4vw, 2.75rem);
  font-weight: 800;
  margin-bottom: 20px;
  color: var(--dark-gray);
}

.cta-content p {
  font-size: 1.25rem;
  margin-bottom: 40px;
  color: var(--dark-gray);
  opacity: 0.9;
}

.cta-buttons {
  display: flex;
  gap: 24px;
  justify-content: center;
  flex-wrap: wrap;
}

.text-center {
  text-align: center;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .container {
    padding: 0 2rem;
  }
  
  .services-grid {
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 1.5rem;
  }
}

@media (max-width: 1024px) {
  .about-content {
    grid-template-columns: 1fr;
    gap: 3rem;
    text-align: center;
  }
  
  .hero-stats {
    gap: 3rem;
  }
  
  .services-grid {
    grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  }
  
  .industries-grid {
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  }
}

@media (max-width: 768px) {
  .container {
    padding: 0 1rem;
  }
  
  .section {
    padding: 4rem 0;
  }
  
  .hero {
    min-height: 100vh;
    padding: 2rem 0;
  }
  
  .hero-stats {
    flex-direction: column;
    gap: 1.5rem;
    margin: 3rem 0;
  }
  
  .stat {
    padding: 1rem 1.5rem;
  }
  
  .hero-buttons {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }
  
  .btn-hero,
  .btn-lg {
    width: 100%;
    max-width: 300px;
  }
 
  
  .services-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .service-content {
    padding: 1.5rem 1rem;
  }
  
  .industries-grid {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 1rem;
  }
  
  .testimonials-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .testimonial-card {
    padding: 2rem 1.5rem;
  }
  
  .cta-buttons {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }
  
  .about-image img {
    height: 300px;
  }
  
  .brands-grid {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 1rem;
  }
  
  .footer-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 0 1rem;
  }
  
  .section {
    padding: 3rem 0;
  }
  
  .hero-title {
    font-size: 2rem;
  }
  
  .hero-subtitle {
    font-size: 1rem;
  }
  
  .section-title {
    font-size: 1.875rem;
  }
  
  .section-subtitle {
    font-size: 1rem;
  }
  
  .service-content {
    padding: 1.25rem 1rem;
  }
  
  .service-content h3 {
    font-size: 1.25rem;
  }
  
  .industry-card,
  .brand-item {
    padding: 1.5rem 1rem;
  }
  
  .testimonial-card {
    padding: 1.5rem 1rem;
  }
  
  .about-features {
    margin-bottom: 2rem;
  }
  
  .feature-item {
    font-size: 0.875rem;
  }
  
  .hero-stats {
    margin: 2rem 0;
  }
  
  .stat {
    padding: 1rem;
  }
  
  .stat-number {
    font-size: 2rem;
  }
  
  .btn-hero,
  .btn-lg {
    padding: 0.875rem 1.5rem;
    font-size: 1rem;
  }
}

@media (max-width: 360px) {
  .container {
    padding: 0 0.75rem;
  }
  
  .hero-title {
    font-size: 1.75rem;
  }
  
  .section-title {
    font-size: 1.5rem;
  }
  
  .services-grid,
  .industries-grid,
  .brands-grid {
    gap: 1rem;
  }
  
  .service-image {
    height: 180px;
  }
}

